.board-image-wrapper {
    display: flex;
}
.board-image img {
    height: 300px;
    margin: 5px;
    object-fit: contain;
}
.board-signature img {
    height: 100px;
    margin: 5px;
    object-fit: contain;
}


.project__wrapper {
    width: 100%;
}
.project__info {
    background-color: transparent;
}
.project_name {
    font-size: 24px;
    font-weight: bold;
}
.project_type {
    font-weight: bold;
}

.project__board__wrapper {
    margin-top: 10px;
}
.card {
    /* Add shadows to create the "card" effect */
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    width: 200px;
    height: 300px;
    border-radius: 10px;
    margin: 5px;
    display: flex;
    flex-direction: column;
}  
.card img {
    width: 100%;
    height: 160px;
    object-fit: contain;
}
  /* On mouse-over, add a deeper shadow */
.card:hover {
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
}
  
  /* Add some padding inside the card container */
.container {
    padding: 2px 16px;
}
.card_title {
    width: 100%;
    height: 40px;
    background-color: #e6e6e6;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
}
.card_header {
    width: 100%;
    height: 110px;
}

.card_colors {
    width: 100%;
    height: 24px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.card_color_left {
    width: 50%;
    height: 100%;
    text-align: center;
    border: 1px solid #e6e6e6;
    line-height: 2;
}
.card_color_right {
    width: 50%;
    height: 100%;
    text-align: center;
    border: 1px solid #e6e6e6;;
}
.card_body {
    width: 100%;
    height: 300px;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
}
.card_footer {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    padding: 5px;
    margin-bottom: 10px;  
}
.text_value {
    font-size: 12px;
    margin-right: 5px;
}
.board__item__wrapper {
    display: flex;
    flex-wrap: wrap;
}
.board_name {
    margin-top: 20px;
    font-size: 22px;
    margin-bottom: 10px;
}
.title_action {
    font-weight: bold;
    font-size: 14px;
    text-align: center;
    margin: 40px;
}
.board_item {
    margin: 5px;
}
.board_item:hover {
    cursor: pointer;
}
.show__wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.show_text {
    width: 100%;
    font-size: 11px;
    color: red;
    text-align: center;
}